import { Routes } from '../helpers/config_keys';

interface ILookUpModel {
	name: string
	label: string
	route: string
}

class LookUpService {
	static get Models(): Array<ILookUpModel> {
		return [
			{ label: "Expense Type", name: "expensetype", route: Routes.GenericSettings },
			{ label: "Product Category", name: "productcategory", route: Routes.GenericSettings },
			//{ label: "Customer Category", name: "customercategory", route: Routes.GenericSettings },
			{ label: "App Setting", name: "appsetting", route: Routes.GenericSettings }
		]
	};
}

export {ILookUpModel, LookUpService}


